import axios from "axios"

const apiClient = axios.create({
    baseURL: 'http://localhost/8080',
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
      "Access-Control-Allow-Origin": "*",
    },
  });

  export default {
    postToken(token) {
        return  apiClient.post("/contacts", token, {
            crossDomain:true
        })
       
        
    },
  };
  