<template>
  <div>
      <NavBar />
      <v-container class="box">
          <div class="display-2">Contatos</div>
          <v-btn
          depressed
          color="primary"
          @click="sair"
          large>sair</v-btn>
      </v-container>
      <v-container>
          <v-data-table
           :headers="headers"
           :items="items">

          </v-data-table>
      </v-container>
  </div>
</template>

<script>
import NavBar from "../components/NavBar"
import { mapState, mapActions} from "vuex";
export default {
    data() {
        return {
            token: this.$route.params.token,
            headers: [
                {text: "Nome", value: "name"},
                {text: "Telefone", value: "phone"}
            ],
            
        }
    },
    computed: {
        ...mapState(['contacts']),
        items() {
            return [
                {name: this.getName(),
                 phone: this.getPhone()},
                
            ]
        }
    },
    components: { NavBar },
    methods: {
        ...mapActions(["postToken"]),
        sair() {
            var auth2 = window.gapi.auth2.getAuthInstance();
            auth2.signOut().then(function () {
            console.log('User signed out.');
            });
            this.$router.push({
                path: "/"
            })
        },
        getName() {
            const names = this.contacts.map(value => value.names)
            for(var a in names) {
                return names[a].map(item => item.displayName)
            }  
        },
        getPhone() {
            const phones = this.contacts.map(value => value.phoneNumbers)
            for (var b in phones) {
                return phones[b].map(item => item.value)
            }
        }
    },
    beforeRouteEnter(to, from, next) {
        next((vm) => {
            vm.token = to.params.token
            vm.contacts = vm.postToken(vm.token)
        })
    },
    beforeRouteUpdate(to, from, next) {
        this.token = to.params.token
        this.contacts = this.postToken(this.token)
        next()
    } 

}
</script>

<style scoped>
.box {
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
}

</style>