<template>
<div>
  <NavBar/>
  <v-container>
    <div class="display-3" id="title">Super OrgContact</div>
    <div 
      class="g-signin2" 
      data-onsuccess="onSignIn" 
      data-theme="dark"
      @click.prevent="onSigIn"></div>
      <v-btn
      class="button"
      @click="goContacts"
      depressed
      color="primary"
      large
    >
      Continue
    </v-btn>
  </v-container>
</div>
</template>

<script>
import NavBar from "../components/NavBar.vue"
export default {
  components: { NavBar },
  metaInfo: {
    title: 'OrgContact',
    meta: [
      {name:"google-signin-scope", content:"profile email"},
      {name:"google-signin-client_id", content:"460610941971-btj6o4lqm1dto0log2et5np057sr1o6o.apps.googleusercontent.com"}
    ],
  },
  methods: {
    onSigIn() {
       // Useful data for your client-side scripts:
        const googleUser = window.gapi.auth2.getAuthInstance().currentUser.get();
        const profile = googleUser.getBasicProfile();
        console.log("ID: " + profile.getId()); // Don't send this directly to your server!
        console.log('Full Name: ' + profile.getName());
        console.log('Given Name: ' + profile.getGivenName());
        console.log('Family Name: ' + profile.getFamilyName());
        console.log("Image URL: " + profile.getImageUrl());
        console.log("Email: " + profile.getEmail())
        console.log(googleUser);
        // The ID token you need to pass to your backend:

        console.log(googleUser.getAuthResponse().access_token)
        

    },
    goContacts() {
    const googleUser = window.gapi.auth2.getAuthInstance().currentUser.get();
    const id_token = googleUser.getAuthResponse().access_token
    if (googleUser.isSignedIn() === true) {
        this.$router.push({
            name: "contacts",
            params: {token: id_token}
        })
    } else {
        window.alert("Faça o login")
    }
    }
  }
}
</script>

