import Vuex from 'vuex'
import Vue from 'vue'
import Service from "../service/axios"

//load Vuex
Vue.use(Vuex);

//to handle state
const state = {
    contacts: []
}

//to handle state
const getters = {}

//to handle actions
const actions = {
    postToken: async ({commit}, token) => {
        return Service.postToken({"token": token})
        .then((response) => 
            commit('SET_CONTACTS', response.data))
    }
}

//to handle mutations
const mutations = {
    SET_CONTACTS(state, contacts) {
    state.contacts = contacts
}
}

//export store module
export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
})